@media (min-width: 1024px) {
  .menu-link, .footer-menu-link {
    position: relative;
  }

  .menu-link:after, .footer-menu-link:after {
    content: "";
    height: 2px;
    width: 100%;
    transform-origin: center;
    background-color: #fff;
    transition: transform .1s;
    display: inline-block;
    position: absolute;
    bottom: -5px;
    left: 0;
    transform: scaleX(0);
  }

  .menu-link:hover:after, .menu-link:active:after, .footer-menu-link:hover:after, .footer-menu-link:active:after {
    transition: transform .25s cubic-bezier(.4, 0, .2, 1);
    transform: scaleX(1);
  }
}

.chk-modal-button {
  display: none;
}

.modal-backdrop {
  width: 100%;
  height: 100%;
  visibility: hidden;
  z-index: 999;
  background-color: #1e2827;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 100%;
  height: 100%;
  flex-direction: column;
  gap: 200px;
  padding: 29px 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.chk-modal-button:checked + .modal-backdrop {
  visibility: initial;
}

.modal-menu-list {
  flex-direction: column;
  align-items: center;
  gap: 14px;
  display: flex;
}

.modal-menu-link {
  letter-spacing: -.02em;
